import uniLoadMore from '../../../components/uni-load-more.vue';
export default {
	 components: {//2注册组件
		uniLoadMore
	},
	data() {
		return {
			banner:{},
			token:'',
			page:1,
			user_points:'',
			allPages:'',
			pointShopList:[],
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	onReachBottom:function(){
		let _this=this;
		_this.pointShop();
	},
	mounted(){
		let _this=this;
		//baner
		_this.getBanner();	
		_this.pointShop();
	},	
	methods: {
		goClassPages:function(url){
			uni.navigateTo({
				url:url,
			})
		},
		exchangeRecords() {
			uni.navigateTo({
				url:"exchange_records",
			})
		},
		integralDetails(url,id) {
			if(id!=''){
				url = url+'?id='+id
			}
			uni.navigateTo({
				url:url
			})
		},
		
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:1,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner=res.data.result.banner[0];
				}
			}).catch(error =>{
				
			})
		},
		//列表
		pointShop:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				page:_this.page,
				
			};
			if (_this.loadingType !== 0) {//loadingType!=0;直接返回
				return false;
			}
			
			_this.loadingType = 1;
			uni.showNavigationBarLoading()
			_this.$axios(_this.$baseUrl.pointShop,data).then(res =>{
				if(res.data.status==1){
					if (res.data.result.integral == null ||res.data.result.integral==undefined ||res.data.result.integral=='' ) {//没有数据
					    _this.loadingType = 2;
					    uni.hideNavigationBarLoading();//关闭加载动画
					    return;
					}
					_this.page++
					_this.pointShopList = _this.pointShopList.concat(res.data.result.integral)
					_this.user_points = res.data.result.user_points;
					_this.allPages = res.data.result.pages;
					_this.loadingType = 0;//将loadingType归0重置
					uni.hideNavigationBarLoading();//关闭加载动画
				}else{
					_this.loadingType = 2;
					uni.hideNavigationBarLoading();//关闭加载动画
				}
			}).catch(error =>{
				
			})
		}
	}
}
